<template > 
    <div>
         <!-- 面包屑导航区域 -->
         <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>添加商品</el-breadcrumb-item>
        </el-breadcrumb>
            <!-- 卡片视图 -->
        <el-card>
            <el-alert   title="添加商品信息" type="info" center show-icon></el-alert>
            <!-- 步骤条 -->
            <el-steps :active="activeIndex - 0" align-center :space="200" finish-status="success">
                <el-step title="基本信息" ></el-step>
                <el-step title="商品参数" ></el-step>
                <el-step title="商品属性" ></el-step>
                <el-step title="商品图片"></el-step>
                <el-step title="商品内容" ></el-step>
                <el-step title="完成"></el-step>
              </el-steps>

              <!-- tabs 区域 -->  
              <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-position="top" label-width="100px">
                <el-tabs :tab-position="tabPosition"  v-model="activeIndex" :before-leave="beforeLeave" @tab-click="tabClickd">
                    <el-tab-pane label="基本信息" name="0">
                        <el-form-item label="商品名称" prop="goods_name">
                            <el-input v-model="addForm.goods_name"></el-input>
                          </el-form-item>
                          <el-form-item label="商品价格" prop="goods_price">
                            <el-input v-model="addForm.goods_price" type="number"></el-input>
                          </el-form-item>
                          <el-form-item label="商品重量" prop="goods_weight">
                            <el-input v-model="addForm.goods_weight" type="number"></el-input>
                          </el-form-item>
                          <el-form-item label="商品数量" prop="goods_number">
                            <el-input v-model="addForm.goods_number" type="number"></el-input>
                          </el-form-item>
                          <el-form-item label="商品分类" prop="goods_cat">
                            <el-cascader v-model="addForm.goods_cat"  :options="cateList" :props="cateProps"  @change="handleChange" style="width:200px"></el-cascader>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品参数" name="1">
                        <el-form-item  :label='item.attr_name' v-for="item in manyTableDate" :key="item.attr_id">
                            <!-- 复选框组  -->
                            <el-checkbox-group v-model="item.attr_vals">
                                <el-checkbox  border :label="cb" v-for="(cb,i) in item.attr_vals" :key="i">
                                </el-checkbox >

                              </el-checkbox-group>
                        </el-form-item>
                        
                    </el-tab-pane>
                    <el-tab-pane label="商品属性" name="2">
                        <el-form-item :label='item.attr_name' v-for="item in onlyTableDate" :key="item.att_id">
                            <el-input v-model="item.attr_vals"></el-input>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="商品图片" name="3">
                        <!-- action 上传的地址 -->
                        <el-upload :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture" :headers="headersObj" :on-success=" handleSuccess" >
                            <el-button size="small" type="primary" >点击上传</el-button>
                        </el-upload>
                    </el-tab-pane>
                    <el-tab-pane label="商品内容" name="4">
                        <!-- 富文本编辑器 -->
                        <quill-editor v-model="addForm.goods_introduce"  />
                        <el-button type="primary" class="btnAdd" @click="add">添加商品</el-button>
                    </el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>

        <!-- 图片预览对话框 -->
        <el-dialog title="图片预览" :visible.sync="previewDialogVisible" width="50%" >
            <img :src="previewPic" alt="" class="previewImg">
        </el-dialog>
    </div>
</template>
<script>
// 导入 lodash包
import _ from 'lodash';
export default {
    data(){
        return {
            tabPosition: 'left',
            // 被激活的标签
            activeIndex:'0',
            addForm:{
                goods_name:'',
                goods_price:0,
                goods_weight:0,
                goods_number:0,
                // 商品分类的分类数组
                goods_cat:[],
                pics:[],
                // 商品详情介绍
                goods_introduce:'',
                attrs:[]

            },
            addFormRules:{
                goods_name:[
                    { required: true, message: '请输入商品名称', trigger: 'blur' },
                ],
                goods_price:[
                    { required: true, message: '请输入商品价格', trigger: 'blur' },
                ],
                goods_weight:[
                    { required: true, message: '请输入商品重量', trigger: 'blur' },
                ], 
                goods_price:[
                    { required: true, message: '请输入商品价格', trigger: 'blur' },
                ],
                goods_number:[
                    { required: true, message: '请输入商品数量', trigger: 'blur' },
                ],
                goods_cat:[
                    { required: true, message: '请输入商品分类', trigger: 'blur' },
                ],
            },
            // 商品分类数据
            cateList:[],
            cateProps:{
                label:'cat_name',
                value:'cat_id',
                children:'children',
                 expandTrigger:"hover" 
            },
            // 动态参数列表数据
            manyTableDate:[],
            // 静态属性列表数据
            onlyTableDate:[],
            // 图片上传的地址
            uploadURL:'http://127.0.0.1:8888/api/private/v1/upload',
            headersObj:{
                Authorization:window.sessionStorage.getItem('token')
            },
            // 被预览的图片
            previewPic:'',
            // 显示与隐藏 被预览对话框
            previewDialogVisible:false,
        }
    },
    methods:{
        //获取商品列表    
        async   getCateList(){
            const {data:res}=    await this.$http.get('categories')
            if(res.meta.status!==200) return this.$message.error('获取分类数据失败')
            this.cateList=res.data    
        },
        // 级联选择器 切换选项
        handleChange(){
            if(this.addForm.goods_cat.length!==3)  {
                this.addForm.goods_cat=[]
            }       
        },
        beforeLeave(activeName,oldActiveName){
            if(oldActiveName==='0' && this.addForm.goods_cat.length!==3){
                this.$message.error('请先选择商品分类')
                return false
            }
        },
        async  tabClickd(){
            //访问了动态参数面板
            if(this.activeIndex ==='1'){
                const {data:res}= await this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:'many'}})
                if(res.meta.status!==200) return this.$message.error('获取数据失败')
                res.data.forEach(item=>{
                    item.attr_vals=item.attr_vals===0?[]:item.attr_vals.split(' ')
                })
                this.manyTableDate=res.data    
            }else if(this.activeIndex ==='2'){
                // 访问了静态属性面板
                const {data:res}= await this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:'only'}})
                if(res.meta.status!==200) return this.$message.error('获取数据失败')
                console.log(res)
                this.onlyTableDate=res.data    
            }

        },
        // 图片预览
        handlePreview(file){
            this.previewPic=file.response.data.url
            this.previewDialogVisible=true
        },
        // 图片移除
        handleRemove(file){
            console.log(file)
            // 1.找到临时路径
            let filePath=file.response.data.tmp_path
            // 2.在pics中找到索引值
            let index= this.addForm.pics.findIndex(item=>{item.pic===filePath})
            // 3.数组删除对应项 
            this.addForm.pics.splice(index,1)
        },
        //监听服务器上传图片
        handleSuccess(response){
            console.log(response)
            let picInfo={pic:response.data.tmp_path}
            this.addForm.pics.push(picInfo)
            console.log(this.addForm)
        },
        async add(){
            this.$refs.addFormRef.validate(async (valid)=>{
                if (!valid) return this.$message.error('请填写必要的表单项！');
                // 预验证完成
                // 执行提交
               const form= _.cloneDeep(this.addForm)
               form.goods_cat = form.goods_cat.join(',')
                //  处理动态参数和静态属性
                this.manyTableDate.forEach(item=>{
                    const attrForm={attr_id:item.attr_id,attr_vals:item.attr_vals.join(' ')}
                    this.addForm.attrs.push(attrForm)
                })
                this.onlyTableDate.forEach(item=>{
                    const attrForm={attr_id:item.attr_id,attr_vals:item.attr_vals}
                    this.addForm.attrs.push(attrForm)
                })
                  form.attrs = this.addForm.attrs
                const  {data : res} = await this.$http.post('goods',form)
                if(res.meta.status!==201){
                    return this.$message.error('添加商品失败!')
                }
                this.$message.success('添加商品成功')
                this.$router.push('/goods')

            })
        }
    },
    created(){
        this.getCateList()
    },
    computed:{
         cateId(){
            if(this.addForm.goods_cat.length===3){
                return  this.addForm.goods_cat[2]
            }
            return null
         }
    }
}
</script>
<style lang="less" scoped>
.el-checkbox{
    margin: 0 5px 0 0 !important;
}
.previewImg{
    width: 100%;
}
.btnAdd{
    margin-top: 15px;
}
</style>