<template > 
    <div>
           <!-- 面包屑导航区域 -->
       <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>分类参数</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区域 -->
        
        <el-card class="box-card">
            <!-- 注意 警示行 -->
          <el-alert  show-icon title="注意：只允许为第三级分类设置相关参数！" type="warning">
          </el-alert>
          <!-- 选择商品分类行 -->
            <el-row class="cat_opt">
                <el-col>
                    <span>选择商品分类：</span>
                    <!-- 需要添加级联选择框 -->
                    <el-cascader :append-to-body="false" :options="catelist" :props="cascaderProps"  v-model="selectedKey" @change="handleChange" clearable   size="medium" ></el-cascader>
                </el-col>
            </el-row>
            <!-- tabs标签页切换 -->
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <!-- 用于添加动态参数 -->
                <el-tab-pane label="动态参数" name="many">
                    <el-button type="primary" :disabled="isBtnDisabled" @click="addDialogVisible=true" >添加参数</el-button>
                    <!-- 动态参数表格 -->
                    <el-table :data="manyTableDate" border  stripe>
                    <!-- 展开行 -->
                        <el-table-column type="expand">
                             <!-- 循环attr_vals -->
                             <template slot-scope="scope">
                                <el-tag   closable v-for="(item,i) in scope.row.attr_vals" :key="i" @close="handleclose(i,scope.row)" >
                                    {{ item }}
                                </el-tag>

                            <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small"  @keyup.enter.native="handleInputConfirm(scope.row)"  @blur="handleInputConfirm(scope.row)" >
                            </el-input>
                            <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

                            </template>
                        </el-table-column>
                    <!-- 索引列 -->
                        <el-table-column type="index" label="#"></el-table-column>
                        <el-table-column  label="动态参数" prop="attr_name"></el-table-column>
                        <el-table-column  label="操作">
                          <template slot-scope="scope">
                            <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.attr_id)">修改</el-button>
                            <el-button size="mini" type="danger" icon="el-icon-delete"  @click="removeParams(scope.row.attr_id)">删除</el-button>
                        </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <!-- 用于添加静态属性 -->
                <el-tab-pane label="静态属性" name="only">
                    <el-button type="primary" :disabled="isBtnDisabled"  @click="addDialogVisible=true">添加属性</el-button>
                    <!-- 静态属性参数表格 -->
                    <el-table :data="onlyTableDate" border  stripe>
                        <!-- 展开行 -->
                        <el-table-column type="expand">
                            <!-- 循环attr_vals -->
                            <template slot-scope="scope">
                                <el-tag   closable v-for="(item,i) in scope.row.attr_vals" :key="i" @close="handleclose(i,scope.row)">
                                    {{ item }}
                                </el-tag>

                                <el-input class="input-new-tag" v-if="scope.row.inputVisible" v-model="scope.row.inputValue" ref="saveTagInput" size="small"  @keyup.enter.native="handleInputConfirm(scope.row)"  @blur="handleInputConfirm(scope.row)">
                                </el-input>
                                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

                            </template>
                        </el-table-column>
                        <!-- 索引列 -->
                        <el-table-column type="index" label="#"></el-table-column>
                        <el-table-column  label="静态属性" prop="attr_name"></el-table-column>
                        <el-table-column  label="操作">
                           <template slot-scope="scope">
                            <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.attr_id)">修改</el-button>
                            <el-button size="mini" type="danger" icon="el-icon-delete"   @click="removeParams(scope.row.attr_id)">删除</el-button>
    
                           </template>
                        </el-table-column>
                        </el-table>
                </el-tab-pane>
              </el-tabs>
        </el-card>
             <!-- 添加对话框 -->
             <el-dialog  :title="'添加'+titleText"  :visible.sync="addDialogVisible"  width="30%" @close="addDialogVisibleClosed">
                <el-form ref="addFormRef" :model="addForm" label-width="80px" :rules="addFormRules" >
                    <el-form-item :label="titleText" prop="attr_name">
                      <el-input v-model="addForm.attr_name"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addDialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="addParams ">确 定</el-button>
                </span>
                </el-dialog>

            <!-- 修改对话框 -->
            <el-dialog  :title="'修改'+titleText"  :visible.sync="editDialogVisible"  width="30%" @close="editDialogVisibleClosed">
            <el-form ref="editFormRef" :model="editForm" label-width="80px" :rules="editFormRules" >
                <el-form-item :label="titleText" prop="attr_name">
                  <el-input v-model="editForm.attr_name"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="editParams ">确 定</el-button>
            </span>
            </el-dialog>
            
    </div>
</template>
<script>
export default {
    data(){
        return {
            // 所有商品分类数据
            catelist:[],
            // 级联选择框属性绑定内容
            cascaderProps:{
                value:'cat_id',
                label:'cat_name',
                children:'children',
                expandTrigger: 'hover'
        },
            // 选中的父级分类的id数组
       selectedKey:[],
            //被激活的页签    
       activeName:'many',
            //存放动态or静态获取到的参数    
       onlyTableDate:[],
       manyTableDate:[],
            //添加属性的对话框    
       addDialogVisible:false,
            //定义添加表单
        addForm:{
            attr_name:'',
            attr_sel:'',
            attr_vals:'',
        },
        addFormRules:{
            attr_name:[ { required: true, message: '请输入商品参数', trigger: 'blur' },],
        },    
        editDialogVisible:false,
        editForm:{},
        editFormRules:{
            attr_name:[ { required: true, message: '请输入商品参数', trigger: 'blur' },],
        },

        }
    },
    methods:{
        // 获取级联选择框所需要的数据
        async getCateList(){
          const {data:res}=  await this.$http.get('categories',{params:{type:3}})
          if(res.meta.status!==200) return this.$message.error('获取数据失败！')  
          this.catelist=res.data
          console.log(res.data)
        },
        // 监听级联选择框 改变事件
         handleChange(){
            //  调用获取参数列表
            this.getParamsData()
        },
            // 单击页签触发事件
        handleClick(){
            console.log(this.activeName)
            //  调用获取参数列表
            this.getParamsData()

        },
        async getParamsData(){
            if(this.selectedKey.length!==3){
                // 不是三级 则无法选中 清空数组 
                this.selectedKey=[]
                // 清空动态属性和静态属性
                this.manyTableDate=[]
                this.onlyTableDate=[]
                return
            }
            // 获取参数列表
          const {data:res}=  await this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:this.activeName}})
            // 分割 由字符串组成的attr_vals
          res.data.forEach(item => {
                item.attr_vals  = item.attr_vals?item.attr_vals.split(' '):[]
                item.inputVisible=false
                item.inputValue=''
            });
          if(res.meta.status!==200) return this.$message.error('获取参数失败！')  
            if(this.activeName=='many'){
                this.manyTableDate=res.data
                console.log(this.manyTableDate)
            }else{
                this.onlyTableDate=res.data
                console.log(this.onlyTableDate)
            }
        },
            // 监听添加的 关闭事件
        addDialogVisibleClosed(){
            this.$refs.addFormRef.resetFields()
        },
            // 添加商品提交
        addParams(){
            this.$refs.addFormRef.validate( async (vaild)=>{
              if (!vaild) return ;
                // 预验证完成 发起请求 
            const{data:res}= await this.$http.post(`categories/${this.cateId}/attributes`,{
                attr_name:this.addForm.attr_name,
                attr_sel:this.activeName,
              })
              if (res.meta.status!=201) return this.$message.error('添加参数失败')
                this.$message.success('添加参数成功')
                 // 关闭对话框 
                    this.addDialogVisible=false
                 // 重新渲染用户列表
                this.getParamsData()
            })
        },
        // 显示修改对话框
         async showEditDialog(attr_id){
            // 查询当前参数信息
            const {data:res}= await this.$http.get(`categories/${this.cateId}/attributes/${attr_id}`)
            if(res.meta.status!==200) return this.$message.error('获取参数失败')
            this.$message.success('获取数据成功')
            this.editForm=res.data
            this.editDialogVisible=true
        },
        editDialogVisibleClosed(){
            this.$refs.editFormRef.resetFields()
        },
        editParams(){
            this.$refs.editFormRef.validate( async (vaild)=>{
              if (!vaild) return ;
                // 预校验成功，发起正式的网络请求 
                const {data:res}= await this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`,{
                    attr_name: this.editForm.attr_name,
                    attr_sel:this.activeName
                })
                if (res.meta.status!=200) return this.$message.error('修改参数失败')
                // 重新渲染用户列表
                this.getParamsData()
                // 关闭对话框  addRolesFormRef
                this.editDialogVisible=false

            })
        },
        // 删除参数
        async removeParams(attr_id){
            const confirmResult= await this.$confirm('此操作将永久删除该参数, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).catch(err => err )
                // 确认删除 返回值为confirm 
                // 取消删除 返回值为cancel 
                if (confirmResult!=='confirm' ){
                return this.$message.info('已取消删除');
            }
              //  删除业务逻辑
              const {data:res}=await this.$http.delete(`categories/${this.cateId}/attributes/${attr_id}`)
                if (res.meta.status!==200) return this.$message.error('删除失败')
                this.$message.success('删除成功')
                // 重新渲染用户列表
                this.getParamsData()     
        },
        // 摁下enter 和失去焦点触发事件
        async handleInputConfirm(row){
            // 如果输入空字符串 会清空并隐藏input
            if(row.inputValue.trim().length==0){
                row.inputValue=''
                row.inputVisible=false
                // 终止后面程序
                return
            }
                // 不是空字符串 继续后面操作
                row.attr_vals.push(row.inputValue.trim())
                row.inputValue=''
                row.inputVisible=false
                this.saveAttrVal(row)
        },
        // 
        async saveAttrVal(row){
                const {data:res}=await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
                    attr_name:row.attr_name,
                    attr_sel:row.attr_sel,
                    attr_vals:row.attr_vals.join(' ')
                })
                if (res.meta.status!=200) return this.$message.error('更新参数失败')
                this.$message.success('更新参数项成功')
                // 重新渲染用户列表
                this.getParamsData()
        },
        handleclose(index,row){
            row.attr_vals.splice(index,1)
            this.saveAttrVal(row)
        
        },
        showInput(row){
            row.inputVisible=true
             //自动获取焦点 
             this.$nextTick(_ => {
                this.$refs.saveTagInput.$refs.input.focus();
            });
        }


    },
    created(){
        // 页面创建获取级联选择框所需要的数据
        this.getCateList()
    },
    computed:{
        // isBtnDisabled 如果被禁用 返回true 否则返回false
        isBtnDisabled(){
            if(this.selectedKey.length!==3){
                return  true
            }else{
                return  false
            }
        },
        cateId(){
            if(this.selectedKey.length===3){
                return  this.selectedKey[2]
            }
            return null
        },
        titleText(){
            if(this.activeName=='many'){
                return '动态参数'
            }else{
                return '静态参数'
            }
        },
    }
}
</script>

<style lang="less" scoped>
.cat_opt{
    margin:  15px 0px;
}
.el-cascader{
    width:200px;
}
.el-cascader-panel{
    height:300px;
}
.el-cascader-menu {
    height: 300px;
}
.el-tag{
    margin: 5px;
}
.input-new-tag{
    width: 120px;
}
</style>