<template> <!-- vue实例外创建 -->
    <div>
           <!-- 面包屑导航区域 -->
           <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>商品列表</el-breadcrumb-item>
        </el-breadcrumb>
         <!-- 卡片视图 -->
         <el-card>
            <!-- 添加角色 独占一行 -->
            <el-row :gutter="20">
                <el-col :span="8">
                        <el-input placeholder="请输入内容" v-model="queryInfo.query" @click="getGoodList">
                          <el-button slot="append" icon="el-icon-search" @click="getGoodList"></el-button>
                        </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="goAddPage">添加商品</el-button>
                </el-col>
            </el-row>
            <!-- 列表区域 -->
            <el-table :data="goodsList"  border stripe style="width: 100%">
            <el-table-column type="index" label="#" ></el-table-column>
            <el-table-column prop="goods_name" label="商品名称" ></el-table-column>
            <el-table-column prop="goods_price" label="商品价格(元)" width="95px"></el-table-column>
            <el-table-column prop="goods_weight" label="商品重量"  width="95px"></el-table-column>
            <el-table-column prop="add_time" label="创建时间"  width="200px">
                <template slot-scope="scope">
                    {{ scope.row.add_time | dateForamt }}
                </template>
            </el-table-column>
            <el-table-column  label="操作"  width="150px"> 
               <template slot-scope="scope">
                <!-- 有编辑提交商品 接口 但是好像是add页面的 -->
                <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>
               </template>
            </el-table-column>
          </el-table>
          <!-- 分页 -->
          <el-pagination    @size-change="handleSizeChange"   @current-change="handleCurrentChange"  :current-page="queryInfo.pagenum"    :page-sizes="[5,10,15,20]"   :page-size="queryInfo.pagesize"  layout="total, sizes, prev, pager, next, jumper"    :total="total" background>
        </el-pagination>
            </el-card>
    </div>
</template>

<script>

export default {
    data(){
        return{
            // 查询商品数据
            queryInfo:{
                query:'',
                pagenum:1,
                pagesize:10
            },
            // 商品总数
            total:0,
            // 查询到的商品列表
            goodsList:[],

        }
    },
    methods:{
        async getGoodList(){
           const {data:res}= await this.$http.get('goods',{params:this.queryInfo})
           if(res.meta.status!==200) return this.$message.error('获取商品列表失败')
           this.$message.success('获取商品列表成功')
           console.log(res.data)
            this.goodsList=res.data.goods
            this.total=res.data.total
        },
        handleSizeChange(newSize){
            this.queryInfo.pagesize=newSize
            this.getGoodList()

        },
        handleCurrentChange(newPage){
            this.queryInfo.pagenum=newPage
            this.getGoodList()
        },
        async removeById(id){
          const confirmResult=  await this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)

            if (confirmResult!=='confirm' ){
                    return this.$message.info('已取消删除');
            }
            const {data:res}=await this.$http.delete(`goods/${id}`)
            if (res.meta.status!==200) return this.$message.error('删除失败')
            this.$message.success('删除成功')
                // 重新渲染用户列表
            this.getGoodList()     

        },
        goAddPage(){
            this.$router.push('/goods/add')
        }
    },
    created(){
        this.getGoodList()
    }
}
</script>
<style lang="less" scoped>

</style>