<template > <!-- vue实例外创建 -->
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>商品分类</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区域  -->
        <el-card class="box-card">
            <div >
                <el-row :gutter="20">
                    <el-col :span="6">
                        <el-button type="primary" @click="addCateDialog">添加分类</el-button>
                    </el-col>
                  
                  </el-row>
            </div>
        <!-- 表单区域 -->
        <tree-table :data="catelist" :columns="columns" :selection-type="false" :expand-type="false" :show-index="true" index-text="#" border show-row-hover class="treeTable" >
            <template slot="isok" slot-scope="scope">
                <!--  scope.row.cat_deleted cat_deleted 是否被删除状态 -->
                <i class="el-icon-success" v-if="scope.row.cat_deleted===false" style="color:lightgreen"></i>
                <i class="el-icon-error" v-else style="color:red"> </i>
            </template>
            <template slot="order" slot-scope="scope">
                <el-tag size="mini" v-if="scope.row.cat_level===0">一级</el-tag>
                <el-tag size="mini" type="success" v-else-if="scope.row.cat_level===1">二级</el-tag>
                <el-tag size="mini" type="warning" v-else>三级</el-tag>
            </template>
            <template slot="opt" slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="editCateDialog(scope.row.cat_id)">编辑</el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeCateById(scope.row.cat_id)">删除</el-button>
            </template>
        </tree-table>
        <!-- 分页区域 -->
        <el-pagination  @size-change="handleSizeChange"  @current-change="handleCurrentChange"  :current-page="queryInfo.pagenum"  :page-sizes="[3, 5, 10, 15]"  :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
        </el-card>
        <!-- 添加分类对话框区域 -->
        <el-dialog  title="添加分类" :visible.sync="addCateDialogVisible" width="30%" @close="addCateDialogClosed"  >
             <!-- 主体对话框 -->
             <el-form  :model="addCateForm" :rules="addCateFormRules" label-width="100px" ref="addCateFormRef">
                <el-form-item label="分类名称：" prop="cat_name">
                <el-input v-model="addCateForm.cat_name"></el-input>
                </el-form-item>
                <!-- :modal-append-to-body='true'  :append-to-body='false' -->
                <el-form-item label="父级分类："   >
                    <!-- 级联选择框  options 用于指定数据源-->
                    <el-cascader :options="parentCateList" :props="cascaderProps"  v-model="selectedKeys" @change="parentCateChange" clearable ></el-cascader>

                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addCateDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="addCate">确 定</el-button>
            </span>
         </el-dialog>
         <!-- 修改分类 对话框区域 -->
         <el-dialog  title="提示"  :visible.sync="editCateDialogVisible" width="50%">
            <el-form  :model="editCateForm" :rules="editCateFormRules" label-width="100px" ref="editCateFormRef" @close="editCateDialogClosed">
                <el-form-item label="分类名称：" prop="cat_name">
                <el-input v-model="editCateForm.cat_name" @change="editCateFormChanged"></el-input>
                </el-form-item>
            </el-form>
            
            <span slot="footer" class="dialog-footer">
                <el-button @click="editCateDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="editCate">确 定</el-button>
            </span>
        </el-dialog>

    </div>
</template>
<script>
   export default {
    data(){
        return{
            // 查询条件
            queryInfo:{
                // 全部获取 默认在第一页 且显示五条数据
                type:3,
                pagenum:1,
                pagesize:5,
            },
            // 查询到的内容 保存在这
            catelist:[],
            // 查询到的数据总数
            total:0,
            //作为每一列的定义 
            columns:[{
                label:'分类名称',
                prop:'cat_name'
            },{
                // 自定义模板  是否有效
                label:'是否有效',
                // 模板类型
                type:'template',
                // 使用的插槽
                template:'isok',
            },{
                // 自定义模板 排序
                label:'排序',
                // 模板类型
                type:'template',
                // 使用的插槽
                template:'order',
            },{
                // 自定义模板 操作
                label:'操作',
                // 模板类型
                type:'template',
                // 使用的插槽
                template:'opt',
            }],
            // 显示，隐藏 添加分类对话框
            addCateDialogVisible:false,
            editCateDialogVisible:false,
            addCateForm:{
                cat_name:'',
                cat_pid:0,
                // 默认0级
                cat_level:0,
            },
            addCateFormRules:{
                cat_name:[
                    { required: true, message: '请输入分类名称', trigger: 'blur' },
                ],
        },
        // 查询到角色
        editCateForm:{        },
        editCateFormRules:{
            cat_name:[
                    { required: true, message: '请输入分类名称', trigger: 'blur' },
                ],
        },
         // 用于存放父元素获取到的数据
        parentCateList:[],
        cascaderProps:{
                value:'cat_id',
                label:'cat_name',
                children:'children',
                expandTrigger: 'hover'
        },
            // 选中的父级分类的id数组
       selectedKeys:[]
        }
    },
    methods:{
        async getCateList(){
          const {data:res}=  await this.$http.get('categories',{params:this.queryInfo})
          if(res.meta.status!==200) return this.$message.error('获取数据失败！')  
          this.catelist=res.data.result
          console.log(res.data)
          this.total=res.data.total
        },
        // 单击添加分类事件
        addCateDialog(){
            // 单机时获取父级列表
            this.getParentCateList()
            this.addCateDialogVisible=true
        },  
        // 监听pagesize变化
        handleSizeChange(newSize){
            this.queryInfo.pagesize=newSize
            this.getCateList()
        },
        // 监听当前的页码 pagenum
        handleCurrentChange(newPage){
            this.queryInfo.pagenum=newPage
            this.getCateList()
        },
        // 获取父级分类列表
        async getParentCateList(){
             const {data:res}=  await this.$http.get('categories',{params:{type:2}})
          if(res.meta.status!==200) return this.$message.error('获取数据失败！')  
            // 保存获取到的数据
            this.parentCateList=res.data
            console.log(this.parentCateList)
        },
        parentCateChange(){
            if(this.selectedKeys.length>0){
            // 选中父级分类
            // 父级分类的id
            this.addCateForm.cat_pid=this.selectedKeys[this.selectedKeys.length-1]
            // 父级分类的等级赋值
            this.addCateForm.cat_level=this.selectedKeys.length
            return
            }else{
            // 没选择父级分类
            this.addCateForm.cat_pid=0
            this.addCateForm.cat_level=0
            }
        },
        // 添加新分类的btn
        addCate(){
            // 表单预验证
            this.$refs.addCateFormRef.validate(async (valid)=>{
                if (!valid) return;
                // 预验证完成
                const {data:res}= await  this.$http.post('categories',this.addCateForm)
                console.log(res)
                if (res.meta.status!=201) return this.$message.error('创建分类失败');
                // 关闭对话框
                this.addCateDialogVisible=false
                // 重新获取数据
                  this.getCateList()
                // 弹出创建分类成功
                this.$message.success('创建分类成功')
            })
        },
        // 监听关闭 添加分类 对话框
        addCateDialogClosed(){
            // 重置表单
            this.$refs.addCateFormRef.resetFields()
            this.selectedKeys=[]
            this.addCateForm.cat_pid=0
            this.addCateForm.cat_level=0
        },
        // editCateDialog 显示修改分类 对话框
        async editCateDialog(id){
            const {data:res} =await this.$http.get('categories/'+id)
                if (res.meta.status!=200) return this.$message.error('获取分类失败')
                this.$message.success('获取分类成功')
                this.editCateForm= res.data

            this.editCateDialogVisible=true
            console.log(this.editCateForm)
        },
        // 监听 修改对话框的change事件
        editCateFormChanged(){
            console.log(this.editCateForm)
        },
        // 提交修改后的数据
        editCate(){
            this.$refs.editCateFormRef.validate(async (valid)=>{
                if (!valid) return;
                const {data : res}= await  this.$http.put('categories/'+this.editCateForm.cat_id,{cat_name:this.editCateForm.cat_name })
            
                if (res.meta.status!=200) return this.$message.error('更新分类失败');
                        // 关闭对话框
                this.editCateDialogVisible = false
                        // 更新数据 只更新表单即可

                        // this.catelist=res.data.result
                this.getCateList()
                this.$message.success('更新分类成功')    
            })
        },
        // 监听关闭 修改 对话框
        editCateDialogClosed(){
             // 重置表单
             this.$refs.editCateFormRef.resetFields()
             this.editCateForm.cat_name=''
        },
          // 删除该分类
          async removeCateById(id){
                const confirmResult= await this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).catch(err => err )
                // 确认删除 返回值为confirm 
                // 取消删除 返回值为cancel 
                if (confirmResult!=='confirm' ){
                return this.$message.info('已取消删除');
                }
                const {data:res}=await   this.$http.delete('categories/'+id)
                if (res.meta.status!=200) return this.$message.error('删除失败')
                this.$message.success('删除成功')
                // 重新渲染用户列表
                this.getCateList()     
        },

    },
    created(){
        // 页面创建获取数据
        this.getCateList()
    },
   }
</script>
<style lang="less" scoped>

.treeTable{
    margin-top: 10px;
}


</style>